.article-title {
    text-align: center;
    margin: 1rem 1rem 1rem 1rem;
}

.image-container {
    margin: 1em 1em 2em 1em;
    overflow: hidden;
}

.image-container img {
    width: 100%;
    position: relative;
    bottom: 50%;
    background-color: lightgrey;
}

@media (max-width: 800px){
    .image-container {
        max-height: 20em;
    }
}

@media (min-width: 800px) {
    .image-container {
        float: left;
        width: 50%;
        max-height: 400px;
    }
}

.article-body {
    margin: 0em;
    padding: 1em;
    white-space: pre-line;
    text-indent: 1em;
}
.article {
    padding: 0 1em 2em 1em;
    border: 1px solid #DDD;
    box-shadow: 1px 1px 10px 0px #DDD;
    overflow: hidden;
}


